n(slide-left, ivuSlideLeft);\n.slide-motion(slide-right, ivuSlideRight);\n\n@keyframes ivuTransitionDropIn {\n    0% {\n        opacity: 0;\n        transform: scaleY(0.8);\n    }\n    100% {\n        opacity: 1;\n        transform: scaleY(1);\n    }\n}\n\n@keyframes ivuTransitionDropOut {\n    0% {\n        opacity: 1;\n        transform: scaleY(1);\n    }\n    100% {\n        opacity: 0;\n        transform: scaleY(0.8);\n    }\n}\n\n@keyframes ivuSlideUpIn {\n    0% {\n        opacity: 0;\n        transform-origin: 0% 0%;\n        transform: scaleY(.8);\n    }\n    100% {\n        opacity: 1;\n        transform-origin: 0% 0%;\n        transform: scaleY(1);\n    }\n}\n\n@keyframes ivuSlideUpOut {\n    0% {\n        opacity: 1;\n        transform-origin: 0% 0%;\n        transform: scaleY(1);\n    }\n    100% {\n        opacity: 0;\n        transform-origin: 0% 0%;\n        transform: scaleY(.8);\n    }\n}\n\n@keyframes ivuSlideDownIn {\n    0% {\n        opacity: 0;\n        transform-origin: 100% 100%;\n        transform: scaleY(.8);\n    }\n    100% {\n        opacity: 1;\n        transform-origin: 100% 100%;\n        transform: scaleY(1);\n    }\n}\n\n@keyframes ivuSlideDownOut {\n    0% {\n        opacity: 1;\n        transform-origin: 100% 100%;\n        transform: scaleY(1);\n    }\n    100% {\n        opacity: 0;\n        transform-origin: 100% 100%;\n        transform: scaleY(.8);\n    }\n}\n\n@keyframes ivuSlideLeftIn {\n    0% {\n        opacity: 0;\n        transform-origin: 0% 0%;\n        transform: scaleX(.8);\n    }\n    100% {\n        opacity: 1;\n        transform-origin: 0% 0%;\n        transform: scaleX(1);\n    }\n}\n\n@keyframes ivuSlideLeftOut {\n    0% {\n        opacity: 1;\n        transform-origin: 0% 0%;\n        transform: scaleX(1);\n    }\n    100% {\n        opacity: 0;\n        transform-origin: 0% 0%;\n        transform: scaleX(.8);\n    }\n}\n\n@keyframes ivuSlideRightIn {\n    0% {\n        opacity: 0;\n        transform-origin: 1