olor: @btn-default-border;\n                }\n            }\n        }\n\n        .@{btn-prefix-cls}-group-vertical &:last-child:not(:first-child),\n        .@{btn-prefix-cls}-group-vertical & + .@{btn-prefix-cls} {\n            border-top-color: @btn-group-border;\n            &[disabled] {\n                border-bottom-color: @btn-default-border;\n            }\n        }\n    }\n\n    &-ghost {\n        .btn-ghost;\n    }\n\n    &-dashed{\n        .btn-dashed;\n    }\n\n    &-text{\n        .btn-text;\n    }\n\n    &-success {\n        .btn-color(@success-color);\n    }\n\n    &-warning {\n        .btn-color(@warning-color);\n    }\n\n    &-error {\n        .btn-color(@error-color);\n    }\n\n    &-info {\n        .btn-color(@info-color);\n    }\n\n    &-circle,\n    &-circle-outline {\n        .btn-circle(@btn-prefix-cls);\n    }\n\n    &:before {\n        position: absolute;\n        top: -1px;\n        left: -1px;\n        bottom: -1px;\n        right: -1px;\n        background: #fff;\n        opacity: 0.35;\n        content: '';\n        border-radius: inherit;\n        z-index: 1;\n        transition: opacity @transition-time;\n        pointer-events: none;\n        display: none;\n    }\n\n    &&-loading {\n        pointer-events: none;\n        position: relative;\n\n        &:before {\n            display: block;\n        }\n    }\n\n    &-group {\n        .btn-group(@btn-prefix-cls);\n    }\n\n    &-group-vertical {\n        .btn-group-vertical(@btn-prefix-cls);\n    }\n}\n",".button-size(@padding; @font-size; @border-radius) {\n    padding: @padding;\n    font-size: @font-size;\n    border-radius: @border-radius;\n}\n\n.button-color(@color; @background; @border) {\n    color: @color;\n    background-color: @background;\n    border-color: @border;\n    // a inside Button which only work in Chrome\n    // http://stackoverflow.com/a/17253457\n    > a:only-child {\n        color: currentColor;\n        &:after {\n            content: '';\n            position: absolute;\n            top: 0;\n            left: 0;\n            bottom: 0;\n            right: 0;\n            background: transparent;\n        }\n    }\n}\n\n.button-variant(@color; @background; @border) {\n    .button-color(@color; @background; @border);\n\n    &:hover\n    //&:focus\n    {\n        .button-color(tint(@color, 20%); tint(@background, 20%); tint(@border, 20%));\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@color, 5%); shade(@background, 5%); shade(@background, 5%));\n    }\n\n    &.disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &:active,\n        &.active {\n            .button-color(@btn-disable-color; @btn-disable-bg; @btn-disable-border);\n        }\n    }\n}\n\n.button-group-base(@btnClassName) {\n    position: relative;\n    display: inline-block;\n    vertical-align: middle;\n    > .@{btnClassName} {\n        position: relative;\n        float: left;\n        &:hover,\n        //&:focus,\n        &:active,\n        &.active {\n            z-index: 2;\n        }\n    }\n\n    & .@{btnClassName}-icon-only .ivu-icon {\n        font-size: 14px;\n        position: relative;\n        top: 1px;\n    }\n\n    &-large .@{btnClassName}-icon-only .ivu-icon{\n        font-size: 16px;\n        top: 2px;\n    }\n\n    &-small .@{btnClassName}-icon-only .ivu-icon{\n        font-size: 12px;\n        top: 0;\n    }\n\n    &-circle .@{btnClassName} {\n        border-radius: @btn-circle-size;\n    }\n\n    // size\n    &-large&-circle .@{btnClassName} {\n        border-radius: @btn-circle-size-large;\n    }\n    &-large {\n        & > .@{btnClassName} {\n            .button-size(@btn-padding-large; @btn-font-size-large; @btn-border-radius);\n        }\n    }\n\n    &-small&-circle .@{btnClassName} {\n        border-radius: @btn-circle-size-small;\n    }\n    &-small {\n        & > .@{btnClassName}{\n            .button-size(@btn-padding-small; @btn-font-size; @btn-border-radius-small);\n            > .@{css-prefix-iconfont} {\n                font-size: @btn-font-size;\n            }\n        }\n    }\n}\n\n.button-group-vertical-base(@btnClassName) {\n    display: inline-block;\n    vertical-align: middle;\n    > .@{btnClassName} {\n        display: block;\n        width: 100%;\n        max-width: 100%;\n        float: none;\n    }\n}\n\n.btn() {\n    display: inline-block;\n    margin-bottom: 0;\n    font-weight: @btn-font-weight;\n    text-align: center;\n    vertical-align: middle;\n    touch-action: manipulation;\n    cursor: pointer;\n    background-image: none;\n    border: 1px solid transparent;\n    white-space: nowrap;\n    line-height: @line-height-base;\n    user-select: none;\n    .button-size(@btn-padding-base; @btn-font-size; @btn-border-radius);\n    //transform: translate3d(0, 0, 0);\n    //transition: all @transition-time linear;\n    transition: color @transition-time linear, background-color @transition-time linear, border @transition-time linear, box-shadow @transition-time linear;\n\n    > .@{css-prefix-iconfont} {\n        line-height: 1;\n    }\n\n    &,\n    &:active,\n    &:focus {\n        outline: 0;\n    }\n\n    &:not([disabled]):hover {\n        text-decoration: none;\n    }\n\n    &:not([disabled]):active {\n        outline: 0;\n        // transition: none;  // 如果不注释此行，那么active会和focus同时触发，此时focus的开始动画transition会无效\n    }\n\n    &.disabled,\n    &[disabled] {\n        cursor: @cursor-disabled;\n        > * {\n            pointer-events: none;\n        }\n    }\n\n    &-large {\n        .button-size(@btn-padding-large; @btn-font-size-large; @btn-border-radius);\n    }\n\n    &-small {\n        .button-size(@btn-padding-small; @btn-font-size; @btn-border-radius-small);\n    }\n}\n\n// Default\n.btn-default() {\n    .button-variant(@btn-default-color; @btn-default-bg; @btn-default-border);\n\n    &:hover\n    //&:focus\n    {\n        .button-color(tint(@primary-color, 20%); white; tint(@primary-color, 20%));\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@primary-color, 5%); white; shade(@primary-color, 5%));\n    }\n    .active-btn-color(@primary-color);\n}\n\n// Primary\n.btn-primary() {\n    .button-variant(@btn-primary-color; @btn-primary-bg; @primary-color);\n\n    &:hover,\n    //&:focus,\n    &:active,\n    &.active {\n        color: @btn-primary-color;\n    }\n    .active-btn-color(@primary-color);\n}\n\n// Ghost\n.btn-ghost() {\n    .button-variant(@btn-ghost-color, @btn-ghost-bg, @btn-ghost-border);\n\n    &:hover\n    //&:focus\n    {\n        .button-color(tint(@primary-color, 20%); @btn-ghost-bg; tint(@primary-color, 20%));\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@primary-color, 5%); @btn-ghost-bg; shade(@primary-color, 5%));\n    }\n    .active-btn-color(@primary-color);\n}\n\n// Dashed\n.btn-dashed() {\n    .button-variant(@btn-ghost-color, @btn-ghost-bg, @btn-ghost-border);\n    border-style: dashed;\n\n    &:hover\n    //&:focus\n    {\n        .button-color(tint(@primary-color, 20%); @btn-ghost-bg; tint(@primary-color, 20%));\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@primary-color, 5%); @btn-ghost-bg; shade(@primary-color, 5%));\n    }\n    .active-btn-color(@primary-color);\n}\n\n// Text\n.btn-text() {\n    .button-variant(@btn-ghost-color, @btn-ghost-bg, transparent);\n\n    // for disabled\n    &.disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &:active,\n        &.active {\n            .button-color(@btn-disable-color; @btn-ghost-bg; transparent);\n        }\n    }\n\n    &:hover\n        //&:focus\n    {\n        .button-color(tint(@primary-color, 20%); @btn-ghost-bg; transparent);\n    }\n    &:active,\n    &.active {\n        .button-color(shade(@primary-color, 5%); @btn-ghost-bg; transparent);\n    }\n    .active-btn-color(@primary-color);\n}\n\n// Color\n// for tabindex\n.active-btn-color(@color) {\n    &:focus {\n        box-shadow: 0 0 0 2px fade(@color, 20%);\n    }\n}\n.btn-color(@color) {\n    .button-variant(@btn-primary-color; @color; @color);\n\n    &:hover,\n        //&:focus,\n    &:active,\n    &.active {\n        color: @btn-primary-color;\n    }\n\n    .active-btn-color(@color);\n}\n\n// Circle for Icon\n.btn-circle(@btnClassName: ivu-btn) {\n    border-radius: @btn-circle-size;\n\n    &.@{btnClassName}-large{\n        border-radius: @btn-circle-size-large;\n    }\n\n    &.@{btnClassName}-size{\n        border-radius: @btn-circle-size-small;\n    }\n\n    &.@{btnClassName}-icon-only{\n        .square(@btn-circle-size);\n        .button-size(0; @font-size-base + 2; 50%);\n\n        &.@{btnClassName}-large{\n            .square(@btn-circle-size-large);\n            .button-size(0; @btn-font-size-large + 2; 50%);\n        }\n\n        &.@{btnClassName}-small{\n            .square(@btn-circle-size-small);\n            .button-size(0; @font-size-base; 50%);\n        }\n    }\n}\n\n// Group\n.btn-group(@btnClassName: ivu-btn) {\n    .button-group-base(@btnClassName);\n\n    .@{btnClassName} + .@{btnClassName},\n    .@{btnClassName} + &,\n    & + .@{btnClassName},\n    & + & {\n        margin-left: -1px;\n    }\n\n    .@{btnClassName}:not(:first-child):not(:last-child) {\n        border-radius: 0;\n    }\n\n    &:not(&-vertical) > .@{btnClassName}:first-child {\n        margin-left: 0;\n        &:not(:last-child) {\n            border-bottom-right-radius: 0;\n            border-top-right-radius: 0;\n        }\n    }\n\n    &:not(&-vertical) > .@{btnClassName}:last-child:not(:first-child) {\n        border-bottom-left-radius: 0;\n        border-top-left-radius: 0;\n    }\n\n    & > & {\n        float: left;\n    }\n\n    & > &:not(:first-child):not(:last-child) > .@{btnClassName} {\n        border-radius: 0;\n    }\n\n    &:not(&-vertical) > &:first-child:not(:last-child) {\n        > .@{btnClassName}:last-child {\n            border-bottom-right-radius: 0;\n            border-top-right-radius: 0;\n            padding-right: 8px;\n        }\n    }\n\n    &:not(&-vertical) > &:last-child:not(:first-child) > .@{btnClassName}:first-child {\n        border-bottom-left-radius: 0;\n        border-top-left-radius: 0;\n        padding-left: 8px;\n    }\n}\n\n.btn-group-vertical(@btnClassName: ivu-btn) {\n    .button-group-vertical-base(@btnClassName);\n\n    .@{btnClassName} + .@{btnClassName},\n    .@{btnClassName} + &,\n    & + .@{btnClassName},\n    & + & {\n        margin-top: -1px;\n        margin-left: 0px;\n    }\n\n    > .@{btnClassName}:first-child {\n        margin-top: 0;\n        &:not(:last-child) {\n            border-bottom-left-radius: 0;\n            border-bottom-right-radius: 0;\n        }\n    }\n\n    > .@{btnClassName}:last-child:not(:first-child) {\n        border-top-left-radius: 0;\n        border-top-right-radius: 0;\n    }\n\n    & > &:first-child:not(:last-child) {\n        > .@{btnClassName}:last-child {\n            border-bottom-left-radius: 0;\n            border-bottom-right-radius: 0;\n            padding-bottom: 8px;\n        }\n    }\n\n    & > &:last-child:not(:first-child) > .@{btnClassName}:first-child {\n        border-bottom-right-radius: 0;\n        border-bottom-left-radius: 0;\n        padding-top: 8px;\n    }\n}\n",".size(@width; @height) {\n    width: @width;\n    height: @height;\n}\n\n.square(@size) {\n    .size(@size; @size);\n}\n\n// fix chrome 12px bug, support ie\n.iconfont-size-under-12px(@size, @rotate: 0deg) {\n    display: inline-block;\n    @font-scale: unit(@size / @font-size-base);\n    font-size: @font-size-base;\n    font-size: ~\"@{size} \\9\"; // ie8-9\n    transform: scale(@font-scale) rotate(@rotate);\n    :root & {\n        font-size: @font-size-base; // reset ie9 and above\n    }\n}\n","@backtop-prefix-cls: ~\"@{css-prefix}back-top\";\n\n.@{backtop-prefix-cls} {\n    z-index: @zindex-back-top;\n    position: fixed;\n    cursor: pointer;\n    display: none;\n\n    &.@{backtop-prefix-cls}-show {\n        display: block;\n    }\n\n    &-inner {\n        background-color: rgba(0,0,0,.6);\n        border-radius: 2px;\n        box-shadow: 0 1px 3px rgba(0,0,0,.2);\n        transition: all @transition-time @ease-in-out;\n\n        &:hover {\n            background-color: rgba(0,0,0,.7);\n        }\n    }\n\n    i{\n        color: #fff;\n        font-size: 24px;\n        padding: 8px 12px;\n    }\n}\n",".ivu-affix {\n    position: fixed;\n    z-index: @zindex-affix;\n}\n","@badge-prefix-cls: ~\"@{css-prefix}badge\";\n\n.@{badge-prefix-cls} {\n    position: relative;\n    display: inline-block;\n    line-height: 1;\n    vertical-align: middle;\n\n    &-count {\n        position: absolute;\n        transform: translateX(50%);\n        top: -10px;\n        right: 0;\n        height: 20px;\n        border-radius: 10px;\n        min-width: 20px;\n        background: @error-color;\n        border: 1px solid transparent;\n        color: #fff;\n        line-height: 18px;\n        text-align: center;\n        padding: 0 6px;\n        font-size: 12px;\n        white-space: nowrap;\n        transform-origin: -10% center;\n        z-index: 10;\n        box-shadow: 0 0 0 1px #fff;\n\n        a,\n        a:hover {\n            color: #fff;\n        }\n\n        &-alone {\n            top: auto;\n            display: block;\n            position: relative;\n            transform: translateX(0);\n        }\n    }\n\n    &-dot {\n        position: absolute;\n        transform: translateX(-50%);\n        transform-origin: 0 center;\n        top: -4px;\n        right: -8px;\n        height: 8px;\n        width: 8px;\n        border-radius: 100%;\n        background: @error-color;\n        z-index: 10;\n        box-shadow: 0 0 0 1px #fff;\n    }\n}\n","@circle-prefix-cls: ~\"@{css-prefix}chart-circle\";\n\n.@{circle-prefix-cls}{\n    display: inline-block;\n    position: relative;\n\n    &-inner {\n        width: 100%;\n        text-align: center;\n        position: absolute;\n        left: 0;\n        top: 50%;\n        transform: translateY(-50%);\n        line-height: 1;\n    }\n}\n","@spin-prefix-cls: ~\"@{css-prefix}spin\";\n@spin-dot-size-small: 12px;\n@spin-dot-size: 20px;\n@spin-dot-size-large: 32px;\n\n.@{spin-prefix-cls} {\n    color: @primary-color;\n    vertical-align: middle;\n    text-align: center;\n\n    &-dot {\n        position: relative;\n        display: block;\n        border-radius: 50%;\n        background-color: @primary-color;\n        .square(@spin-dot-size);\n        animation: ani-spin-bounce 1s 0s ease-in-out infinite;\n    }\n\n    &-large &-dot {\n        .square(@spin-dot-size-large);\n    }\n\n    &-small &-dot {\n        .square(@spin-dot-size-small);\n    }\n\n    &-fix {\n        position: absolute;\n        top: 0;\n        left: 0;\n        z-index: @zindex-spin;\n        .square(100%);\n        background-color: rgba(255,255,255,.9);\n    }\n    &-fullscreen{\n        z-index: @zindex-spin-fullscreen;\n        &-wrapper{\n            position: fixed;\n            top: 0;\n            right: 0;\n            bottom: 0;\n            left: 0;\n        }\n    }\n\n    &-fix &-main {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        -ms-transform: translate(-50%, -50%);\n        transform: translate(-50%, -50%);\n    }\n\n    &-fix &-dot {\n        display: inline-block;\n    }\n\n    &-text,\n    &-show-text &-dot {\n        display: none;\n    }\n\n    &-show-text &-text {\n        display: block;\n    }\n}\n\n// use in Table loading, Table right border is not included in .ivu-table-wrapper, so fix it\n.@{table-prefix-cls}-wrapper{\n    > .@{spin-prefix-cls}-fix{\n        border: 1px solid @border-color-base;\n        border-top: 0;\n        border-left: 0;\n    }\n}\n\n@keyframes ani-spin-bounce {\n    0% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(1);\n        opacity: 0;\n    }\n}\n","@alert-prefix-cls: ~\"@{css-prefix}alert\";\n@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n.@{alert-prefix-cls}{\n    position: relative;\n    padding: 8px 48px 8px 16px;\n    border-radius: @border-radius-base;\n    color: @text-color;\n    font-size: @font-size-small;\n    line-height: 16px;\n    margin-bottom: 10px;\n\n    &&-with-icon{\n        padding: 8px 48px 8px 38px;\n    }\n\n    &-icon {\n        font-size: @font-size-base;\n        top: 8px;\n        left: 16px;\n        position: absolute;\n    }\n\n    &-desc {\n        font-size: @font-size-small;\n        color: @text-color;\n        line-height: 21px;\n        display: none;\n        text-align: justify;\n    }\n\n    &-success {\n        border: 1px solid tint(@success-color, 80%);\n        background-color: tint(@success-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @success-color;\n        }\n    }\n\n    &-info {\n        border: 1px solid tint(@primary-color, 80%);\n        background-color: tint(@primary-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @primary-color;\n        }\n    }\n\n    &-warning {\n        border: 1px solid tint(@warning-color, 80%);\n        background-color: tint(@warning-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @warning-color;\n        }\n    }\n\n    &-error {\n        border: 1px solid tint(@error-color, 80%);\n        background-color: tint(@error-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @error-color;\n        }\n    }\n\n    &-close {\n        .content-close(-3px);\n    }\n\n    &-with-desc {\n        padding: 16px;\n        position: relative;\n        border-radius: @border-radius-base;\n        margin-bottom: 10px;\n        color: @text-color;\n        line-height: 1.5;\n    }\n\n    &-with-desc&-with-icon{\n        padding: 16px 16px 16px 69px;\n    }\n\n    &-with-desc &-desc{\n        display: block;\n    }\n\n    &-with-desc &-message {\n        font-size: 14px;\n        color: @title-color;\n        display: block;\n    }\n\n    &-with-desc &-icon {\n        top: 50%;\n        left: 24px;\n        margin-top: -21px;\n        font-size: 28px;\n    }\n\n    &-with-banner{\n        border-radius: 0;\n    }\n}","@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n.content-header() {\n    border-bottom: 1px solid @border-color-split;\n    padding: 14px 16px;\n    line-height: 1;\n\n    p,\n    &-inner\n    {\n        display: inline-block;\n        width: 100%;\n        height: 20px;\n        line-height: 20px;\n        font-size: @font-size-base;\n        color: @title-color;\n        font-weight: bold;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n    }\n}\n\n.content-close(@top: 0, @icon-font-size: 22px) {\n    font-size: @font-size-small;\n    position: absolute;\n    right: 16px;\n    top: 8px;\n    overflow: hidden;\n    cursor: pointer;\n\n    .@{icon-prefix-cls}-ios-close-empty {\n        .close-base(@top, @icon-font-size);\n    }\n}",".close-base(@top: 0, @icon-font-size: 22px) {\n    font-size: @icon-font-size;\n    color: @legend-color;\n    transition: color @transition-time ease;\n    position: relative;\n    top: @top;\n    &:hover {\n        color: #444;\n    }\n}","@collapse-prefix-cls: ~\"@{css-prefix}collapse\";\n\n.@{collapse-prefix-cls}{\n    background-color: @background-color-base;\n    border-radius: 3px;\n    border: 1px solid @border-color-base;\n\n    & > &-item{\n        border-top: 1px solid @border-color-base;\n        &:first-child {\n            border-top: 0;\n        }\n\n        > .@{collapse-prefix-cls}-header{\n            height: 38px;\n            line-height: 38px;\n            padding-left: 32px;\n            color: #666;\n            cursor: pointer;\n            position: relative;\n\n            > i{\n                transition: transform @transition-time @ease-in-out;\n            }\n        }\n    }\n    &  > &-item&-item-active > &-header > i{\n        transform: rotate(90deg);\n    }\n\n    &-content{\n        //display: none;\n        //overflow: hidden;\n        color: @text-color;\n        padding: 0 16px;\n        background-color: #fff;\n\n        & > &-box {\n            padding-top: 16px;\n            padding-bottom: 16px;\n        }\n    }\n    &-item-active > &-content{\n        //display: block;\n    }\n    &-item:last-child {\n        > .@{collapse-prefix-cls}-content {\n            border-radius: 0 0 3px 3px;\n        }\n    }\n}\n","@card-prefix-cls: ~\"@{css-prefix}card\";\n\n.@{card-prefix-cls}{\n    background: #fff;\n    border-radius: @border-radius-small;\n    font-size: @font-size-base;\n    position: relative;\n    //overflow: hidden;\n    transition: all @transition-time @ease-in-out;\n\n    &-bordered {\n        border: 1px solid @border-color-base;\n        border-color: @border-color-split;\n    }\n\n    &-shadow{\n        box-shadow: @shadow-card;\n    }\n\n    &:hover {\n        box-shadow: @shadow-base;\n        border-color: #eee;\n    }\n    &&-dis-hover:hover{\n        box-shadow: none;\n        border-color: transparent;\n    }\n\n    &&-dis-hover&-bordered:hover{\n        border-color: @border-color-split;\n    }\n\n    &&-shadow:hover{\n        box-shadow: @shadow-card;\n    }\n\n    &-head {\n        .content-header;\n    }\n\n    &-extra {\n        position: absolute;\n        right: 16px;\n        top: 14px;\n    }\n\n    &-body {\n        padding: 16px;\n    }\n}","@message-prefix-cls: ~\"@{css-prefix}message\";\n@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n.@{message-prefix-cls} {\n    font-size: @font-size-small;\n    position: fixed;\n    z-index: @zindex-message;\n    width: 100%;\n    top: 16px;\n    left: 0;\n    pointer-events: none;\n\n    &-notice {\n        padding: 8px;\n        text-align: center;\n        transition: height @animation-time @ease-in-out, padding @animation-time @ease-in-out;\n\n        &:first-child {\n          margin-top: -8px;\n        }\n\n        &-close {\n            position: absolute;\n            right: 4px;\n            top: 9px;\n            color: #999;\n            outline: none;\n\n            i.@{icon-prefix-cls}{\n                .close-base(-3px);\n            }\n        }\n    }\n\n    &-notice-content {\n        display: inline-block;\n        pointer-events: all;\n        padding: 8px 16px;\n        //border: 1px solid @border-color-split;\n        border-radius: @border-radius-small;\n        box-shadow: @shadow-base;\n        background: #fff;\n        position: relative;\n        &-text{\n            display: inline-block;\n        }\n    }\n\n    &-notice-closable{\n        .@{message-prefix-cls}-notice-content-text{\n            padding-right: 32px;\n        }\n    }\n\n    &-success .@{icon-prefix-cls} {\n        color: @success-color;\n    }\n\n    &-error .@{icon-prefix-cls} {\n        color: @error-color;\n    }\n\n    &-warning .@{icon-prefix-cls} {\n        color: @warning-color;\n    }\n\n    &-info .@{icon-prefix-cls},\n    &-loading .@{icon-prefix-cls} {\n        color: @primary-color;\n    }\n\n    .@{icon-prefix-cls} {\n        margin-right: 8px;\n        font-size: 14px;\n        top: 1px;\n        position: relative;\n    }\n}","@notice-prefix-cls: ~\"@{css-prefix}notice\";\n@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n@notice-width: 335px;\n@notice-padding: 16px;\n@notice-margin-bottom: 10px;\n\n.@{notice-prefix-cls} {\n    width: @notice-width;\n    margin-right: 24px;\n    position: fixed;\n    z-index: @zindex-notification;\n\n    &-content-with-icon{\n        margin-left: 51px;\n    }\n    &-with-desc&-with-icon &-title{\n        margin-left: 51px;\n    }\n\n    &-notice {\n        margin-bottom: @notice-margin-bottom;\n        padding: @notice-padding;\n        // border: 1px solid @border-color-split;\n        border-radius: @border-radius-small;\n        box-shadow: @shadow-base;\n        background: #fff;\n        line-height: 1;\n        position: relative;\n        overflow: hidden;\n\n        &-close {\n            position: absolute;\n            right: 16px;\n            top: 15px;\n            color: #999;\n            outline: none;\n\n            i{\n                .close-base(-3px);\n            }\n        }\n\n        &-content-with-render{\n            .ivu-notice-desc{\n                display: none;\n            }\n        }\n\n        &-with-desc{\n            .@{notice-prefix-cls}-notice-close{\n                top: 11px;\n            }\n        }\n    }\n\n    &-content-with-render-notitle{\n        margin-left: 26px;\n    }\n\n    &-title {\n        font-size: @font-size-base;\n        line-height: @font-size-base + 3;  //fixed the bug that the bottom of some letters were hidden just like 'g'\n        color: @title-color;\n        padding-right: 10px;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n    }\n    &-with-desc &-title{\n        font-weight: bold;\n        margin-bottom: 8px;\n    }\n\n    &-desc {\n        font-size: 12px;\n        //color: @legend-color;\n        color: @text-color;\n        text-align: justify;\n        line-height: 1.5;\n    }\n    &-with-desc&-with-icon &-desc{\n        margin-left: 51px;\n    }\n\n    &-with-icon &-title{\n        margin-left: 26px;\n    }\n\n    &-icon {\n        position: absolute;\n        left: 20px;\n        margin-top: -1px;\n        font-size: 16px;\n\n        &-success {\n            color: @success-color;\n        }\n        &-info {\n            color: @primary-color;\n        }\n        &-warning {\n            color: @warning-color;\n        }\n        &-error {\n            color: @error-color;\n        }\n    }\n    &-with-desc &-icon{\n        font-size: 36px;\n    }\n\n    &-custom-content{\n        &:after{\n            content: \"\";\n            display: block;\n            width: 4px;\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n        }\n    }\n    &-with-normal{\n        &:after{\n            background: @primary-color;\n        }\n    }\n    &-with-info{\n        &:after{\n            background: @primary-color;\n        }\n    }\n    &-with-success{\n        &:after{\n            background: @success-color;\n        }\n    }\n    &-with-warning{\n        &:after{\n            background: @warning-color;\n        }\n    }\n    &-with-error{\n        &:after{\n            background: @error-color;\n        }\n    }\n}","@radio-prefix-cls: ~\"@{css-prefix}radio\";\n@radio-group-prefix-cls: ~\"@{radio-prefix-cls}-group\";\n@radio-inner-prefix-cls: ~\"@{radio-prefix-cls}-inner\";\n@radio-group-button-prefix-cls: ~\"@{radio-group-prefix-cls}-button\";\n\n.@{radio-prefix-cls}-focus {\n    box-shadow: 0 0 0 2px fade(@primary-color, 20%);\n    z-index: 1;\n}\n\n.@{radio-group-prefix-cls} {\n    display: inline-block;\n    font-size: @font-size-small;\n    vertical-align: middle;\n    //outline: none;\n    &-vertical{\n        .@{radio-prefix-cls}-wrapper {\n            display: block;\n            height: 30px;\n            line-height: 30px;\n        }\n    }\n}\n\n// 普通状态 - Normal state\n.@{radio-prefix-cls}-wrapper {\n    font-size: @font-size-small;\n    vertical-align: middle;\n    display: inline-block;\n    position: relative;\n    white-space: nowrap;\n    margin-right: 8px;\n    cursor: pointer;\n    &-disabled{\n        cursor: @cursor-disabled;\n    }\n    //outline: none;\n}\n\n.@{radio-prefix-cls} {\n    display: inline-block;\n    margin-right: 4px;\n    white-space: nowrap;\n    //outline: none;\n    position: relative;\n    line-height: 1;\n    vertical-align: middle;\n    cursor: pointer;\n    &:hover {\n        .@{radio-inner-prefix-cls} {\n            border-color: #bcbcbc;\n        }\n    }\n    &-inner {\n        display: inline-block;\n        width: 14px;\n        height: 14px;\n        position: relative;\n        top: 0;\n        left: 0;\n        background-color: #fff;\n        borde