t-number-prefix-cls}{\n        float: right;\n        margin-top: -14px;\n    }\n}\n","@cascader-prefix-cls: ~\"@{css-prefix}cascader\";\n@cascader-item-prefix-cls: ~\"@{css-prefix}cascader-menu-item\";\n\n.selectDropDown{\n    width: auto;\n    padding: 0;\n    white-space: nowrap;\n    overflow: visible;\n}\n\n.@{cascader-prefix-cls} {\n    //position: relative;\n    line-height: normal;\n\n    &-rel{\n        display: inline-block;\n        width: 100%;\n        position: relative;\n    }\n\n    .@{css-prefix}input{\n        display: block;\n        cursor: pointer;\n    }\n    &-disabled .@{css-prefix}input{\n        cursor: @cursor-disabled;\n    }\n\n    &-label{\n        width: 100%;\n        height: 100%;\n        line-height: 32px;\n        padding: 0 7px;\n        box-sizing: border-box;\n        white-space: nowrap;\n        text-overflow: ellipsis;\n        overflow: hidden;\n        cursor: pointer;\n        font-size: @font-size-small;\n        position: absolute;\n        left: 0;\n        top: 0;\n    }\n    &-size-large &-label{\n        line-height: 36px;\n        font-size: @font-size-base;\n    }\n    &-size-small &-label{\n        line-height: 26px;\n    }\n\n    .@{cascader-prefix-cls}-arrow:nth-of-type(1) {\n        display: none;\n        cursor: pointer;\n    }\n\n    &:hover {\n        .@{cascader-prefix-cls}-arrow:nth-of-type(1) {\n            display: inline-block;\n        }\n    }\n    &-show-clear:hover .@{cascader-prefix-cls}-arrow:nth-of-type(2){\n        display: none;\n    }\n\n    &-arrow {\n        .inner-arrow();\n    }\n    &-visible &-arrow:nth-of-type(2) {\n        transform: rotate(180deg);\n    }\n\n    .@{select-dropdown-prefix-cls} {\n        .selectDropDown();\n    }\n\n    .select-item(@cascader-prefix-cls, @cascader-item-prefix-cls);\n    .ivu-select-item span{\n        color: @error-color;\n    }\n\n    &-dropdown{\n        padding: 5px 0;\n        .@{select-dropdown-prefix-cls}-list{\n            max-height: 190px;\n