tem-prefix-cls}-error{\n    .@{cascader-prefix-cls} {\n        &-arrow{\n            color: @error-color;\n        }\n    }\n}","@transfer-prefix-cls: ~\"@{css-prefix}transfer\";\n@transfer-item-prefix-cls: ~\"@{css-prefix}transfer-list-content-item\";\n\n.@{transfer-prefix-cls} {\n    position: relative;\n    line-height: @line-height-base;\n\n    &-list{\n        display: inline-block;\n        width: 180px;\n        height: 210px;\n        font-size: @font-size-small;\n        vertical-align: middle;\n        position: relative;\n        padding-top: 35px;\n\n        &-with-footer{\n            padding-bottom: 35px;\n        }\n\n        &-header {\n            padding: 8px 16px;\n            background: @head-bg;\n            color: @text-color;\n            border: 1px solid @border-color-base;\n            border-bottom: 1px solid @border-color-split;\n            border-radius: @border-radius-base @border-radius-base 0 0;\n            overflow: hidden;\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n\n            &-title{\n                cursor: pointer;\n            }\n\n            & > span{\n                padding-left: 4px;\n            }\n\n            &-count {\n                margin: 0 !important;\n                float: right;\n            }\n        }\n\n        &-body{\n            height: 100%;\n            border: 1px solid @border-color-base;\n            border-top: none;\n            border-radius: 0 0 @border-radius-base @border-radius-base;\n            position: relative;\n            overflow: hidden;\n\n            &-with-search{\n                padding-top: 34px;\n            }\n            &-with-footer{\n                border-radius: 0;\n            }\n        }\n\n        &-content{\n            height: 100%;\n            padding: 4px 0;\n            overflow: auto;\n\n            &-item{\n                overflow: hidden;\n                white-space: nowrap;\n               